
          @import "@/styles/global.sass"
        
















































































































































































































































































































































































































































































































































.code
  height: 100%
  opacity: 0
  pointer-events: none
  position: relative
  outline: none
  transition: opacity .5s ease-in
  transition-delay: .7s


  &.ready
    opacity: 1

  // #0 ::v-deep
  //   .underScore
  //     display: inline-block
  //     font-size: 0.9em
  //     transform: translateY(4px) !important
  //     filter: saturate(70%)
  //   .underScoreHidden
  //     opacity: 0
  //   .CodeMirror-linenumber
  //     opacity: 0


.codemirror ::v-deep
  .CodeMirror-gutters
    border: none
  .CodeMirror-cursor
    border-left: 1px solid #FFCC00
    opacity: 1
    border-right: none

  .CodeMirror-selected
    background: transparent

  .CodeMirror-linenumber
    font-size: 20px
    line-height: 22px
    font-weight: normal

  .CodeMirror-line
    // line-break: anywhere !important

    span > span
      transition: filter 1s ease-out
      filter: grayscale(40%) brightness(80%)
      font-size: 20px
      line-height: 22px
      font-weight: normal

    .correct
      filter: none

    .next-char
      border-bottom: 2px solid white

    .mistake
      background-color: rgba(255, 255, 255, .3)


  .CodeMirror-scroll
    padding-right: .7em

  .CodeMirror-vscrollbar
    &::-webkit-scrollbar
      width: $gap / 2 !important
    &::-webkit-scrollbar-thumb
      background: linear-gradient(to top, $purple-gradient-colors) !important
    &::-webkit-scrollbar-track
      background-color: $grid-color !important
    &::-webkit-scrollbar-corner
      background-color: $grid-color !important



.completed
  pointer-events: auto
  .codemirror ::v-deep

    .CodeMirror-line

      span > span
        transition: filter 1s ease-out 2s

      .next-char
        border-bottom: none

      .corrected
        background-color: lighten($grid-color, 4%)
        opacity: 1
        display: inline-block
        filter: none
        padding: 0.1em 0

        transition: padding $nav-trans-dur $nav-trans-timing 2s, background-color .4s ease-out 3s

        //maintain that order
        &.alone
          padding: 0.1em 0.2em

        &.left-most
          padding-left: 0.2em

        &.right-most
          padding-right: 0.2em

.pop-up
  position: fixed
  top: 0
  bottom: 0
  left: 0
  right: 0
  background-color: rgba($grid-color, .5)
  display: flex
  justify-content: space-around
  align-items: center
  pointer-events: all
  user-select: none
  z-index: 10
  font-size: 4rem
  opacity: 1
  animation: opacity-enter .7s ease-out forwards
  cursor: default
  text-align: center

  &.hidden
    pointer-events: none
    animation: opacity-leave .4s ease-out forwards

  &.clickable
    // pointer-events: all
    h2
      cursor: pointer
  &.small-font
    font-size: 1.5rem

    h2
      max-width: 50vw
      text-align: center

  p
    animation: opacity-enter .5s ease-out forwards .7s
    animation-fill-mode: backwards
    font-size: 2rem
    transform: translateY(4rem)

